

.services__top-content{
    text-align: center;
    margin-bottom: 40px;
}

.services__item{
    background: var(--card-bg);
    width: 28%;
    padding: 30px 15px;
    text-align: center;
    border-radius: .5rem;

}


.service__item-wrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;
    column-gap: 2rem;
}

.services__icon{
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    background: var(--icon-bg);
    border-radius: 5px;
    margin-bottom: 30px;
    border: 1px solid var(--primary-color);
}

.light-theme .services__icon{
    background-color: var(--primary-color);
}

.light-theme .services__icon i{
    color: #fff;
}

.services__icon i{
    font-size: 2rem;
    color: var(--primary-color);
}

.service__title{
    color: var(--primary-color);
    font-size: 1.3rem;
    margin-bottom: 30px;
}


@media  only screen and (max-width:992px) {
    .services__top-content h2{
        font-size: 1.5rem;
    }
    .service__item-wrapper{
        flex-wrap: wrap;
    }
    .services__item{
        width: 30%;
        margin-bottom: 30px;
    }
}


@media  only screen and (max-width:768px){
    .services__item{
        width: 47.7%;
    }
    .service__title{
        font-size: 1.2rem;
    }
}

@media  only screen and (max-width:576px){
    .services__item{
        width: 100%;
    }
}

