@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
/* google fonts */

/* css variables */
:root {
  --primary-color: #379cf6;
  --heading-color: #fff;
  --btn-primary-bg: #379cf6;
  --btn-secondary-bg: #090238;
  --team-card-bg: #1b2352;
  --link-color: #fff;
  --link-active: #379cf6;
  --card-bg: linear-gradient(
    45deg,
    rgba(15, 18, 61, 1) 33%,
    rgba(15, 18, 61, 1) 52%,
    rgba(11, 41, 69, 1) 84%
  );
  --counter-color: linear-gradient(
    90deg,
    #02bba6 30%,
    rgba(29, 129, 176, 1) 55%,
    rgba(35, 93, 173, 1) 75%
  );
  --body-bg: rgba(6, 11, 26, 1);
  --newsletter-bg: linear-gradient(
    90deg,
    rgba(27, 9, 115, 1) 20%,
    rgba(23, 9, 96, 1) 38%,
    rgba(14, 9, 56, 1) 100%
  );
  --testimonial-bg: #1a0f4f;
  --font-name: "Roboto", sans-serif;
  --small-text-color: rgba(255, 255, 255, 0.774);
  --icon-bg:#151e4f;
}

.light-theme {
  --body-bg: rgb(255, 255, 255);
  --heading-color: #0c123d;
  --btn-secondary-bg: #816aff;
  --btn-primary-bg: #fff;
  --primary-color: #816aff;
  --counter-color: #fff;
  --counter-section-bg: #816aff;
  --link-color: #0c123d;
  --link-active: #816aff;
  --newsletter-bg: linear-gradient(
    180deg,
    rgba(189, 243, 255, 1) 29%,
    rgba(193, 243, 255, 1) 50%,
    rgba(226, 250, 255, 1) 78%
  );
  --small-text-color:#000;
  --card-bg:#fff;
  --testimonial-bg: #f7f7f7;
  --team-card-bg: #fff;
}

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
}



body{
    background: rgba(6, 11, 26, 1);
    background: var(--body-bg);
    font-family: "Roboto", sans-serif;
    font-family: var(--font-name);
}

a{
    text-decoration: none;
    color: #fff;
    color: var(--link-color);
}

h1,h2{
    color: #fff;
    color: var(--heading-color);
    font-size: 2rem;
}

section{
    padding: 60px 0px;
}




.container{
    width: 1140px;
    padding: 0px 15px;
    margin: auto;
}

.description{
    color: rgba(255, 255, 255, 0.774);
    color: var(--small-text-color);
    line-height: 30px;
    font-size: .9rem;
}

.subtitle{
    font-size: 1rem;
    color: #379cf6;
    color: var(--primary-color);
    font-weight: 400;
    margin-bottom: 20px;
}

.light-theme .counter{
    background: var(--counter-section-bg);
}

.light-theme .secondary__btn{
    border: 1px solid #379cf6;
    border: 1px solid var(--primary-color);
    color: #090238;
    font-weight: 600;
}

.light-theme a{
    font-weight: 500;
}

.light-theme .team__img{
    background: #ddd;
}


@media  only screen and (max-width:1024px) {
  .container{
    width: 100%;
  }

}

@media  only screen and (max-width:768px){
  section{
    padding: 40px 0px;
  }
}
.header{
    width: 100%;
    height:80px;
    line-height: 80px;
}

.nav__wrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.menu{
    display: flex;
    align-items: center;
    grid-column-gap: 2.7rem;
    -webkit-column-gap: 2.7rem;
            column-gap: 2.7rem;
    list-style: none;
}

.menu__link{
    font-weight: 400;
    font-size: .9rem;
    cursor: pointer;
}

.menu__link:hover{
    color: var(--link-active);
}

.logo h2{
    color: var(--primary-color);
}

.light__mode span{
    color: #ffffff62;
    display: flex;
    align-items: center;
    grid-column-gap: .4rem;
    -webkit-column-gap: .4rem;
            column-gap: .4rem;
    font-size: 0.8rem;
    cursor: pointer;
}


.light-theme .light__mode span{
    color: #000;
    font-weight: 500;
}

.header__shrink{
    width: 100%;
    height: 80px;
    line-height: 80px;
    position:fixed;
    top: 0;
    left: 0;
    z-index: 9999;
    background: #0c123d;
}



.mobile__menu{
    color: #fff;
    font-style: 1.3rem;
    cursor: pointer;
    display: none;
}

.light-theme .header__shrink{
    background: #f7f7f7;
    box-shadow: 2px 2px 10px -2px grey;
}

.light-theme .mobile__menu{
    color: #000;
}

 /* ===========================  responsive =================================== */

 @media  only screen and (max-width:992px) {



    .navigation{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(6,11,26, 0.868);
        z-index: 99999;
        display: none;
    }
    
    .menu{
        flex-direction: column;
        background: #0c123d;
        width: 250px;
        height: 100%;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 0;
        right: 0;
        z-index: 999999;
    }

    .menu__active{
        display: block;
    }

    .mobile__menu{
        display: block;
    }

    .light-theme .menu{
        background: white;
    }
 }


 @media  only screen and (max-width:768px) {
    
    .header{
        width: 100%;
        height: 50px;
        line-height: 50px;
    }

    .mobile__menu{
        font-style: 1rem;
    }

    .logo h2{
        font-style: 1.3rem;
    }
 }




.hero__wrapper{
    display: flex;
    justify-content: space-between;
}

.hero__content, .hero__img{
    width: 50%;
}

.hero__img img{
    width: 100%;
}



.hero__content{
    padding-top: 40px;
}

.hero__content h2{
    font-size: 3rem;
    line-height: 65px;
}

.hero__content p{
   margin-top: 40px;
   font-size: 1.1rem;
}

.highlight{
    color: var(--primary-color);
}

.hero__btns{
    margin-top: 40px;
    display: flex;
    align-items: center;
    grid-column-gap: 2rem;
    -webkit-column-gap: 2rem;
            column-gap: 2rem;
}

.primary__btn, .secondary__btn{
    padding: 0.8rem 1.5rem;
    border: none;
    outline: none;
    color: #fff;
    border-radius: 0.2rem;
    cursor: pointer;
}

.primary__btn{
    background: var(--btn-secondary-bg);
    border: 1px solid var(--btn-primary-bg);
}

.secondary__btn{
    background: var(--btn-primary-bg);
}



@media  only screen and (max-width:992px) {
    .hero__content h2{
        font-size: 2rem;
        line-height: 50px;
    }
}

@media  only screen and (max-width:768px) {

    .hero__content h2{
        font-size: 1.5rem;
        line-height: 45px;
    }

    .hero__content p{
        font-size: 0.9rem;
    }

    .primary__btn, .secondary__btn{
        font-size: 0.8rem;
    }

 
}

@media  only screen and (max-width:600px){
    .hero__content, .hero__img{
        width: 100%;
    }

   
    .hero__wrapper{
        flex-direction: column;
    }
}
.counter__wrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.counter__item{
    width: 33%;
    text-align: center;
}

.counter__number, .counter__title{
    background: var(--counter-color);
    background-size: 100% 100%;
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-text-fill-color: transparent;
}

.counter__number{
    font-size: 2.5rem;
    margin-bottom:10px;
}

.counter__title{
    font-size: 1.5rem;
}


@media  only screen and (max-width:992px) {
    .counter__number{
        font-size: 2rem;
    }
    .counter__title{
        font-size: 1.2rem;
    }
    .counter__item{
      
        width: 30%;
    }
}

@media  only screen and (max-width:768px){
    .counter__number{
        font-size: 1.3rem;
    }
    .counter__title{
        font-size: 1rem;
    }
}


@media  only screen and (max-width:576px){
    .counter__wrapper{
        flex-direction: column;
    }

    .counter__item{
        width: 100%;
        margin-bottom: 30px;
    }

}


.services__top-content{
    text-align: center;
    margin-bottom: 40px;
}

.services__item{
    background: var(--card-bg);
    width: 28%;
    padding: 30px 15px;
    text-align: center;
    border-radius: .5rem;

}


.service__item-wrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;
    grid-column-gap: 2rem;
    -webkit-column-gap: 2rem;
            column-gap: 2rem;
}

.services__icon{
    width: 50px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    background: var(--icon-bg);
    border-radius: 5px;
    margin-bottom: 30px;
    border: 1px solid var(--primary-color);
}

.light-theme .services__icon{
    background-color: var(--primary-color);
}

.light-theme .services__icon i{
    color: #fff;
}

.services__icon i{
    font-size: 2rem;
    color: var(--primary-color);
}

.service__title{
    color: var(--primary-color);
    font-size: 1.3rem;
    margin-bottom: 30px;
}


@media  only screen and (max-width:992px) {
    .services__top-content h2{
        font-size: 1.5rem;
    }
    .service__item-wrapper{
        flex-wrap: wrap;
    }
    .services__item{
        width: 30%;
        margin-bottom: 30px;
    }
}


@media  only screen and (max-width:768px){
    .services__item{
        width: 47.7%;
    }
    .service__title{
        font-size: 1.2rem;
    }
}

@media  only screen and (max-width:576px){
    .services__item{
        width: 100%;
    }
}


.about__wrapper{
    display: flex;
    grid-column-gap: 2rem;
    -webkit-column-gap: 2rem;
            column-gap: 2rem;

}

.about__content, .about__img{
       width: 50%;

} 



.about__img img{
    width: 100%;
    height: 75%;
    object-fit: cover;
    border-radius: .8rem;
}

.about__content-desc{
    margin-top: 30px;
}

.choose__us-item{
    padding: 20px 20px 20px 0px;
    display: flex;
    align-items: center;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
    margin-top: 15px;
}

.choose_-item-wrapper{
    margin-top: 20px;
}

.choose__us-icon{
    width: 50px;
    height: 50px;
    padding: 0px 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid var(--primary-color);
    border-radius: 5px;
}
.choose__us-icon i{
    color: var(--primary-color);
    font-size: 2rem;
}

.choose__us-title{
    color: #fff;
    font-weight: 500;
}

.light-theme .choose__us-title{
    color: #0c123d !important;
}


@media  only screen and (max-width:992px) {
    .about__content h2{
        font-size: 1.5rem;
    }
}

@media  only screen and (max-width:768px) {
   .about__wrapper{
    flex-direction: column;
   }
   .about__content,.about__img{
    width: 100%;
   }
   .about__content{
    margin-bottom: 30px;
   }
   .about__img img{
    height: 400px !important;
   }
}



.our__team{
    padding-top: 0;
    margin-bottom: 50px;
}

.team__content{
    text-align: center;
    margin-bottom: 50px;
}

.team__item{
    width: 25%;
    background: var(--team-card-bg);
    border-radius: 5px;
}

.team__img{
   width: 100%;
   height: 200px;
   border-radius: 5px;
   background: white;
   overflow: hidden;
   padding-top: 10px;
}

.team__img img{
    width: 100%;
    height: 170px;
    object-fit: contain;
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}

.team__details{
    padding: 20px 15px;
}

.team__details h4{
    color: var(--primary-color);
    font-weight: 500;
    font-style: 1.2rem;
    margin-bottom: .4rem;
}

.team__member-social{
    display: flex;
    align-items: center;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
    margin-top: 5px;
}

.team__member-social span i{
    padding: 5px;
    background: var(--icon-bg);
    color: #fff;
    border-radius: 3px;
    cursor: pointer;

}

.team__wrapper{
    display: flex;
    align-items: center;
    grid-column-gap: 2rem;
    -webkit-column-gap: 2rem;
            column-gap: 2rem;
}


@media  only screen and (max-width:768px) {
    .team__item{
        width: 30%;
        margin-bottom: 30px;
    }
    .team__wrapper{
        flex-wrap: wrap;
    }
    .team__content h2{
       font-size: 1.5rem;
    }
    
    .team__details h4{
           font-size: 1rem;
    }

    .team__details p{
        font-size: 0.8rem;
    }
}

@media  only screen and (max-width:576px){
          .team__item{
            width: 100%;
          }
}


.blog__top-content{
    text-align: center;
    margin-bottom: 50px;
}

.blog__item{
    width: 30%;
    padding: 30px 25px;
    background: var(--card-bg);
    border-radius: .5rem;
    cursor: pointer;
}

.blog__item h3{
    color: whitesmoke;
    font-weight: 400;
    font-size:1.5rem;
    margin-bottom: 20px;
}

.blog__img{
    margin-bottom: 30px;
}


.blog__img img{
    width: 100%;
}
.blog__desc{
    font-size: 1.1rem;
    margin-bottom: 20px;
}


.learn__more i{
    font-size: 1.8rem;
    color: var(--primary-color);
    cursor: pointer;
}

.blog__wrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.blog__item:nth-child(2) .blog__img{
    margin-top: -100px;
    margin-bottom: -10px;
}

.blog__item:nth-child(2){
    width: 33%;
    margin-top: -70px;
}

.light-theme .blog{
    background: #9ec8d2;
}

.light-theme .blog__item{
    box-shadow: 5px 5px 15px -5px #999;
}

.light-theme .blog__item:nth-child(2){
    border: 1px solid var(--primary-color);
}

@media  only screen and (max-width:992px){
           .blog__top-content h2{
            font-size: 1.5rem;
           }
}


@media  only screen and (max-width:768px){
           .blog__item{
            width: 45%;
            margin-bottom: 30px;
           }
           .blog__item:nth-child(2){
            width: 50%;
           }
           .blog__wrapper{
            flex-wrap: wrap;
           }
}


@media  only screen and (max-width:576px){
  .blog__item, .blog__item:nth-child(2){
   width: 100%;


  }

  .blog__item:nth-child(2){
    margin-top: 0;
  }
}

.slider__content-top{
