    border-radius: .5rem;
    text-align: center;
    cursor: pointer;
}



.customer__details{
    display: flex;
    align-items: center;
    justify-content: center;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
    margin-top: 40px;
}

.customer__img{
