    }
}

@media  only screen and (max-width:768px){
       .slider__wrapper{
        width: 90%;
       }

       .customer__name{
        font-size: 1rem;
       }
}


.newsletter{
   background: var(--newsletter-bg);
}


.newsletter__wrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;
    
}

.newsletter_content, .newsletter__form{
