   }
}


@media  only screen and (max-width:768px){
    .newsletter__wrapper{
        flex-wrap: wrap;
    }

    .newsletter_content, .newsletter__form{
        width: 100%;
    }

    .newsletter_content{
        margin-bottom: 30px;
    }

    .newsletter__form{
        padding: 8px 20px;font-style: 0.9rem;
    }
}


.footer{
