

.newsletter{
   background: var(--newsletter-bg);
}


.newsletter__wrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;
    
}

.newsletter_content, .newsletter__form{
          width: 50%;
}

.newsletter__form input{
   padding: .7rem 1.5rem;
   border: none;
   outline: none;
   font-style: 1rem;
}

.newsletter__form{
    padding: 0.4rem;
    background: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 0.5rem;
}


.light-theme .newsletter{
    background:  linear-gradient(
        180deg,
        rgba(189, 243, 255, 1) 29%,
        rgba(193, 243, 255, 1) 50%,
        rgba(226, 250, 255, 1) 78%
      );
}

.light-theme .subscribe__btn{
    background: var(--primary-color);
    color: #fff;
}

@media  only screen and (max-width:992px){
   .newsletter_content h2{
    font-size: 1.5rem;
   }
}


@media  only screen and (max-width:768px){
    .newsletter__wrapper{
        flex-wrap: wrap;
    }

    .newsletter_content, .newsletter__form{
        width: 100%;
    }

    .newsletter_content{
        margin-bottom: 30px;
    }

    .newsletter__form{
        padding: 8px 20px;font-style: 0.9rem;
    }
}